#hero-texts {

  .counter {
    margin-top: 5rem;
  
    &>div:not(:last-child) {
      margin-right: 2.25rem;
    }
  }
  
  .btn.btn-shadow {
    box-shadow: 0 0.5rem 0.9375rem rgba(50, 82, 223, 0.3);
  }
}

#hero-banner {

  .image-hero-container {
    width: 90%;
    height: 110%;
    padding: 3.125rem;
    position: absolute;
    transform: scale(0.85);
    margin-top: -3.3125rem;
  
    #image-hero {
      width: 100%;
      height: 100%;
      border-radius: $border-radius*25 $border-radius*3.25 $border-radius*3.25 $border-radius*3.25;
      margin: -1.875rem 0 0 -1.875rem;
      background-size: 200%;
      background-position: 45% 35%;
      position: inherit;
      right: -0.5625rem;
      z-index: 1;
    }
  
    #image-hero-frame {
      width: 100%;
      height: 100%;
      border: 2px solid $gray-300;
      border-radius: $border-radius*3.25;
      margin: 0 -0.9375rem -0.9375rem 0;
      position: inherit;
      right: -1.4375rem;
    }
  }
}